.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.items li{
  color: black;
  float: left;
  width: 31.5%;
  margin: 1px;
  padding: 10px;
  display: table-cell;
  height: 240px;
  text-align: left;
}
.items li img{
  display: block;
  width: 150px;
  border-radius: 44%;

}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
